*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  background-color: #f7f7f7;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  display: flex;
}

.go-back {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.go-back a {
  color: #333;
  background-color: #e7e7e7;
  border-radius: 5px;
  padding: 8px 15px;
  font-weight: bold;
  text-decoration: none;
  transition: background-color .3s;
}

.go-back a:hover {
  background-color: #d7d7d7;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s, transform .3s;
}

[data-start] {
  color: #fff;
  background-color: #4caf50;
}

[data-stop] {
  color: #fff;
  background-color: #f44336;
}

button:hover {
  transform: scale(1.05);
}

button:active {
  transform: scale(.95);
}

button:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

